.c-footer {
  text-align: center;
  background: #000;
  color: #fff;
  padding: 0.266667rem 20%;
}

[data-dpr="1"] .c-footer {
  font-size: 13px;
}

[data-dpr="2"] .c-footer {
  font-size: 26px;
}

[data-dpr="3"] .c-footer {
  font-size: 39px;
}

@media screen and (max-width: 800px) {
  .c-footer {
    padding: 0.266667rem 0;
  }

  .c-footer span {
    display: block;
  }
}


