.c-nav {
  height: 1.546667rem;
}

.c-nav--hide {
  display: none;
}

.c-nav__inner {
  position: fixed;
  width: 100%;
  z-index: 999;
  background: #1b813e;
  height: 1.546667rem;
  line-height: 1.546667rem;
  overflow: hidden;
}

[data-dpr="1"] .c-nav__inner {
  font-size: 16px;
}

[data-dpr="2"] .c-nav__inner {
  font-size: 32px;
}

[data-dpr="3"] .c-nav__inner {
  font-size: 48px;
}

.c-nav__inner:after {
  content: '';
  display: block;
  clear: both;
}

.c-nav__left {
  float: left;
  color: #fff;
  padding-left: 8vw;
}

@media screen and (max-width: 800px) {
  .c-nav__left {
    padding-left: 0.533333rem;
  }
}

.c-nav__text {
  font-size: 0.453333rem;
}

@media screen and (max-width: 800px) {
  .c-nav__text {
    display: none;
  }
}

.c-nav__logo {
  vertical-align: middle;
  -webkit-transform: translateY(-0.093333rem);
      -ms-transform: translateY(-0.093333rem);
          transform: translateY(-0.093333rem);
  margin-right: 0.133333rem;
}

@media screen and (max-width: 800px) {
  .c-nav__logo {
    display: none;
  }
}

.c-nav__list {
  float: right;
  padding-right: 5vw;
  text-align: center;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (max-width: 800px) {
  .c-nav__list {
    float: none;
    padding-right: 0;
    font-size: 0.346667rem;
  }
}

.c-nav__link {
  color: #fff;
  text-decoration: none;
  margin: 0 0.346667rem;
  display: inline-block;
}

@media screen and (max-width: 800px) {
  .c-nav__link {
    margin: 0 0.266667rem;
  }
}

.c-nav__link.active {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}


